/* member-profile */
.member-profile {
  .member-head {
    .member__profile-img {
      img {
        max-width: 140px;
        height: auto;
        border: 1px solid $gray2;
        padding: 2.5px;
        transition: background-color 0.3s ease-in-out;
        &:hover {
          background-color: $orange1;
        }
      }
    }
  } //member-head

  .member-details {
    .about {
      .about__head {
      }
      .about__body {
        border: 1px solid $gray2;
        border-radius: 15px 0 15px 15px;
        overflow: hidden;
        p {
          padding: 15px;
          background-color: $gray3;
          color: $gray2;
          margin-bottom: 0;
          font-size: 16px;
        }
      }
    }
    .attached {
      border: 1px solid $gray2;
      border-radius: 15px;
      overflow: hidden;
      .accordion {
        .card {
          border: none;
          &:not(:last-of-type) {
            border-bottom: 1px solid $gray2;
          }
          &:first-of-type {
            border-top-left-radius: 15px;
            border-top-right-radius: 15px;
          }
          &:last-of-type {
            border-bottom-left-radius: 15px;
            border-bottom-right-radius: 15px;
          }
          .card-header {
            background-color: $white;
            border-bottom: none;
            .btn-link--custom {
              text-decoration: none;
              color: $orange1;
              padding-bottom: 15px;
              svg {
                max-width: 55px;
                height: auto;
              }

              .expanded {
                transition: transform 0.3s ease-in-out;
                width: 22px;
                height: 18px;
                border-radius: 50%;
              }
              &[aria-expanded="true"] {
                .expanded {
                  transform: rotate(-45deg);
                }
              }
            }
          }
        }
      }
    }
    .follow {
      .follow__head {
        span {
          span {
            svg {
              width: 20px;
            }
          }
        }
      }
      .follow__body {
        border: 1px solid #585858;
        border-radius: 15px 0 15px 15px;
        padding: 10px;
        .follow__ref {
          text-align: center;
          padding: 15px;
          border: 1px solid $gray2;
          border-radius: 15px;
          svg {
            width: 80%;
            height: auto;
          }
        }
      }
    }
    .contact {
      .contact__head {
        span {
          span {
            svg {
              width: 30px;
              left: auto;
              right: auto;
              position: relative;
            }
          }
        }
      }
      .contact__body {
        border: 1px solid #585858;
        border-radius: 15px 0 15px 15px;
        padding: 10px;
        i {
          color: $gray2;
        }
        h6 {
          font-weight: bold;
          border-bottom: 1px solid $gray2;
          padding-top: 15px;
          padding-bottom: 15px;

          span {
            padding-inline-start: 10px;
          }
        }
        ul {
          li {
            padding-top: 5px;
            padding-bottom: 5px;
            span {
              padding-inline-start: 10px;
            }
          }
        }
      }
    }
  }

  .client-rating {
    .client__posts {
      .client__post {
        border: 1px solid #585858;
        border-radius: 15px 0 15px 15px;
        padding: 15px 35px;
        margin-bottom: 30px;
        .client__data {
          border-bottom: 1px solid $gray2;
          padding-bottom: 10px;
          img {
            border: 1px solid $gray2;
            max-width: 80px;
            padding: 2px;
          }
          .rate-number {
            background-color: $orange1;
            border-radius: 50%;
            text-align: center;
            width: 30px;
            height: 30px;
            display: inline-block;
            color: #fff;
            text-align: center;
            line-height: 2.5;
            position: relative;
            &::before {
              content: "";
              width: 0;
              height: 0;
              border-top: 6px solid transparent;
              border-left: 12px solid $orange1;
              border-bottom: 6px solid transparent;
              position: absolute;
              right: -9px;
              top: 28%;
            }
          }
          .client-name {
            color: $orange1;
            font-size: 16px;
            margin-right: 15px;
            margin-left: 15px;
          }
          .post__share {
            border-radius: 50%;
          }
        }
        .client__rate{

          .rate{
            svg{
              width: 14px;
            }
          }
        }

      }
    }
  }
}

/* // member-profile */
